@import url(https://cdnjs.cloudflare.com/ajax/libs/bulma/0.8.2/css/bulma.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: rgb(250, 250,250);
}


.container {
  margin-top: 2rem;
}
.card-container {
  margin: 0 2% !important;
  padding: 5px 10px;
}
@media only screen and (max-width: 770px) {
  .card-container {
    margin: 2% 0 !important;
    padding: 5px 10px;
    min-width: 285px;
  }
}

.title {
    color: rgba(0, 0, 0, 0.54);
    font-size: 1rem;
}

.value {
    font-size: 1.5rem;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-weight: 400;
    line-height: 1.334;
    letter-spacing: 0em;
}

.date {
    font-size: 1rem;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-weight: 400;
    line-height: 1.5;
    letter-spacing: 0.00938em;
}

.subTitle {
    font-size: 0.875rem;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-weight: 400;
    line-height: 1.43;
    letter-spacing: 0.01071em;
}
.cards-container {
    display: -webkit-flex;
    display: flex;
    /* flex-direction: column; */
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    margin: 50px 0;
}
@media only screen and (max-width: 770px) {
  .cards-container {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
  }
}
  
.infected {
  /* border-bottom: 10px solid rgba(0, 0, 255, 0.5); */
  border: 3px solid rgba(0, 0, 255, 0.5);
  /* background-color: rgb(238, 230, 230); */
}
  
.infected:hover {
 box-shadow: 10px 10px 5px #aaa;
}

.recovered{
  /* border-bottom: 10px solid rgba(0, 255, 0, 0.5); */
  border: 3px solid rgba(0, 255, 0, 0.5);
}

.recovered:hover {
  box-shadow: 10px 10px 5px #aaa;
}
  
.deaths{
  /* border-bottom: 10px solid rgba(255, 0, 0, 0.5); */
  border: 3px solid rgba(255, 0, 0, 0.5);
}

.deaths:hover {
  box-shadow: 10px 10px 5px #aaa;
}

  
.formControl {
    width:100%;
    margin-bottom: 30px !important;
}

.text-show {
    text-align: center;
}
.cf-container {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.image {
  width: 370px;
  margin-top: 50px;
}

@media only screen and (max-width: 770px) {
  .container {
    margin: 0 10%;
  }

  .image {
    width: 100%;
  }
}
